'use client'
import { useParams } from 'react-router-dom';
import { useState, useEffect } from 'react';
import { getDetails, getRecommendations, getCredits, getProviders, getReviews } from '@/lib/tmdb';
import DetailsClient from './DetailsClient';

export default function DetailsPage() {
  const { type, id } = useParams(); // Obtiene los parÃ¡metros de la URL
  const [data, setData] = useState(null);
  const [recommendations, setRecommendations] = useState(null);
  const [castData, setCastData] = useState(null);
  const [providers, setProviders] = useState(null);
  const [reviews, setReviews] = useState(null);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const [details, recommendationsData, credits, providersData, reviewsData] = await Promise.all([
          getDetails(type, id),
          getRecommendations(type, id),
          getCredits(type, id),
          getProviders(type, id),
          getReviews(type, id)
        ]);

        setData(details);
        setRecommendations(recommendationsData);
        setCastData(credits);
        setProviders(providersData);
        setReviews(reviewsData);
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    };

    if (type && id) {
      fetchData();
    }
  }, [type, id]);

  if (!data) {
    return <div>Loading...</div>;
  }

  return (
    <DetailsClient
      type={type}
      id={id}
      data={data}
      recommendations={recommendations}
      castData={castData}
      providers={providers}
      reviews={reviews}
    />
  );
}
